//(hj)
// 异步的路由信息， 同时，左侧菜单也会根据这个来显示
let asyncRouter;
let tempData = [];
// 导航守卫，渲染动态路由
router.beforeEach((to, from, next) => {
  console.log("asyncRouter", asyncRouter);
  if (whiteList.indexOf(to.path) !== -1) {
    next();
  } else {
    if (!asyncRouter) {
      asyncRouter = tempData;
      if (!(asyncRouter && asyncRouter.length > 0)) {
        asyncRouter = [];
      }
      go(to, next);
    } else {
      next();
    }
  }
});
function go(to, next) {
  asyncRouter = filterAsyncRouter(asyncRouter);
  router.addRoutes(asyncRouter);
  console.log(router.options.routes, "查看现有路由");
  next({ ...to, replace: true });
}

function filterAsyncRouter(routes, parent) {
  return routes.filter((route) => {
    console.log(route, "route");
    const component = route.component;
    if (component) {
      if (typeof component === "string") {
        console.log("string", component);
        route.component = view(component);
      }
      if (route.children && route.children.length) {
        route.children = filterAsyncRouter(route.children, route);
      }
      return true;
    }
  });
}
function view(path) {
  return function (resolve) {
    require([`@/views/${path}.vue`], resolve);
  };
}

export default router
