export default {
    namespaced: true,
    state: {
      targetTables:[],   //目标表
      myTables:[],   //原始表
      fileType:'',  //当前解析的类型
      mdbId:null,  //入库的mdb文件的id
      tableData:[], //存储匹配处理后的表
    },
    mutations: {
      setTargetTables(state, val) {
        state.targetTables = val;
        console.log("state.targetTables",state.targetTables);
      },
      setMyTables(state,val) {
        state.myTables = val;
        console.log('state.myTables',state.myTables)
      },
      setFileType(state,val) {
        state.fileType = val;
        console.log('state.fileType',state.fileType)
      },
      setMdbId(state,val){
        state.mdbId = val;
        console.log('state.mdbId',state.mdbId);
      },
      setTableData(state,val){
        state.tableData = val;
        console.log('state.tableData',state.tableData);
      }
    }
}
