export default {
    namespaced: true,
    state: {
      geoResults: [], // 成果列表
      nodeInfo:{},
      nodeSpatical:{}, //当前节点的坐标信息
    },
    mutations: {
      setGeoResults(state, results) {
        state.geoResults = results;
        console.log("state.geoResults",state.geoResults);
      },
      setNodeInfo(state,val) {
        state.nodeInfo = val;
        console.log('nodeInfo',state.nodeInfo)
      },
      setNodeSpatical(state, val) {
        state.nodeSpatical = val;
        console.log('nodeSpaticalInfo',state.nodeSpatical);
      }
    }
  }